body {
	background-color:black;
	color: #ffffff;
}

.container{
  background-color: black;
}

.overlay{
  position: absolute;
  background: rgba(0,0,0,0.8);
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  bottom: 0;
  font-size:20px;
  padding: 20px;
  text-align: center;
}

.nom{
  position: relative;
  transition: transform 0.2s;
}

.button:hover{
  transform: scale(0.5);
}

.nom:hover{
  cursor: pointer;
  transform: scale(1.1);
}

